"use client";

import Image from "next/image";
import { Menu, X } from "lucide-react";
import { useState } from "react";

export default function MenuVertical() {
  const [open, setOpen] = useState(false);

  return (
    <>
      {/* BOTÃO MOBILE (FIXO NO TOPO) */}
      <button
        onClick={() => setOpen(true)}
        className="md:hidden fixed top-4 left-4 z-50 bg-zinc-900 text-white p-2 rounded-lg shadow-lg"
      >
        <Menu className="w-6 h-6" />
      </button>

      {/* CONTAINER PRINCIPAL */}
      <aside
        className={`fixed md:static top-0 left-0 z-40 h-screen w-64 bg-white dark:bg-zinc-900 border-r border-zinc-300 dark:border-zinc-700 shadow-lg transform transition-transform duration-300 ease-in-out flex flex-col ${
          open ? "translate-x-0" : "-translate-x-full md:translate-x-0"
        }`}
      >
        {/* TOPO: LOGO + ÍCONE */}
        <div className="flex items-center justify-between p-4 border-b border-zinc-200 dark:border-zinc-700">
          <div className="flex items-center gap-2">
            <Image src="/next.svg" alt="Logo" width={32} height={32} />
            <span className="text-lg font-bold text-zinc-800 dark:text-zinc-100">
              Brand
            </span>
          </div>
          {/* BOTÃO FECHAR */}
          <button
            onClick={() => setOpen(false)}
            className="text-zinc-700 dark:text-zinc-200 md:hidden"
          >
            <X className="w-6 h-6" />
          </button>
        </div>

        {/* ITENS DE MENU */}
        <nav className="flex flex-col grow p-4 text-zinc-700 dark:text-zinc-200 font-medium gap-3">
          <a className="hover:text-blue-600 cursor-pointer">Perfil</a>
          <a className="hover:text-blue-600 cursor-pointer">Postagens</a>
          <a className="hover:text-blue-600 cursor-pointer">Amigos</a>
          <a className="hover:text-blue-600 cursor-pointer">Fotos</a>
          <a className="hover:text-blue-600 cursor-pointer">Vídeos</a>
          <a className="hover:text-blue-600 cursor-pointer">Configurações</a>
          <a className="hover:text-blue-600 cursor-pointer">Mensagens</a>
          <a className="hover:text-blue-600 cursor-pointer">Notificações</a>
        </nav>

        {/* RODAPÉ */}
        <div className="p-4 border-t border-zinc-200 dark:border-zinc-700 text-sm text-zinc-500 dark:text-zinc-400">
          © 2025 MeuApp
        </div>
      </aside>

      {/* BACKDROP (SOMBRA AO ABRIR MENU) */}
      {open && (
        <div
          onClick={() => setOpen(false)}
          className="fixed inset-0 bg-black/40 backdrop-blur-sm z-30 md:hidden"
        ></div>
      )}
    </>
  );
}
